import {
    ACCOUNT,
    DOMAINS,
    ADDRESS,
    SCRIPT_HASH,
    LOGIN_STATUS,
    EMPTY_USER,
    INVITER_SCRIPT_HASH,
    DONAT_TOTAL,
    INVITER_TOTAL,
    BALANCE
} from "./types";

export default {
    [LOGIN_STATUS](state, data) {
        state.loginStatus = data
    },
    [ACCOUNT](state, payload) {
        state.account = payload.account
    },
    [ADDRESS](state, payload) {
        state.address = payload.address
    },
    [SCRIPT_HASH](state, payload) {
        state.scriptHash = payload.scriptHash
    },
    [DOMAINS](state, list) {
        state.domains = list || []
    },
    [INVITER_SCRIPT_HASH](state, value) {
        state.inviterScriptHash = value
    },
    [EMPTY_USER](state) {
        state.loginStatus = false
        state.account = {}
        state.address = ""
        state.scriptHash = ""
    },
    [DONAT_TOTAL](state, value) {
        state.donatTotal = value || 0
    },
    [INVITER_TOTAL](state, value) {
        state.inviterTotal = value || 0
    },
    [BALANCE](state, data) {
        state.balance = data
    },
}
