/**
 * 验证域名
 * @param {String} domain
 */
export function verifyDomain(domain) {
    const reg = /^[a-z?(0-9)]{3,30}$/;
    return reg.test(domain);
}

/**
 * 匹配全域名
 * @param {String} domain 
 */
export function verifyDomainAll(domain) {
    const reg = /^(?=^.{3,30}$)[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.ont)+$/
    return reg.test(domain);
}

/**
 * 验证地址
 * @param {String} address
 */
export function verifyAddress(address) {
    const reg = /^[0-9a-zA-Z]{34}$/g;
    return reg.test(address);
}
