import { client } from "ontology-dapi"

const contract = CONFIG.contract; // 合约
const typeMap = new Map([
    ["get", "invokeRead"],
    ["GET", "invokeRead"],
    ["post", "invoke"],
    ["POST", "invoke"]
]);

/**
 *ontology
 *
 * @export
 * @param {*} {
 *     args = [], 参数
 *     type = 'get', 读取类型 不需要上链执行，利用预执行，就能立即得到结果。调用后的返回值即为结果
 *     type = 'post' 写类型 需要上链执行
 *     method api方法名
 * }
 * @returns
 */
export function ontDApi({ args = [], type = "get", method }, contractP) {
    return new Promise((resolve, reject) => {
        const _contract = contractP || contract
        const options = {
            ..._contract, // 展开合约对象
            operation: method, // 方法
            args // 参数
        };
        // console.log(options);
        client.api.smartContract[typeMap.get(type)](options)
        .then(res => resolve(res), err => reject(err))
        .catch(err => reject(err))
    })
}