import store from '@/store'
import { ontDApi } from '@/tools/ontDapi'
import { client } from 'ontology-dapi'

export default {
    /**
     * 捐赠
     * @param {Number} value
     */
    donat(value) {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },       // 调用者自身的scripthash
            { type: 'Number', value: value },                           // 域名名称，例如test123.ont 注册的name为test123
        ]
        return ontDApi({
            args,
            type: 'post',
            method: 'Donat'
        })
    },

    /**
     *获取捐赠总额
     * @param {*} userHash
     * @returns
     */
    getDonatTotal(userHash) {
        const args = [
            { type: 'ByteArray', value: userHash },                     // 查询用户hash
        ]
        return ontDApi({
            args,
            method: 'GetDonatCountForUser'
        })
    },

    /**
     * 获取余额 ont, ong
     */
    async getBalance() {
        return await client.api.network.getBalance({address: store.state.address});
    },

    /**
     * 转账
     * @param {Number} amount
     * @param {String} toHash
     * @param {String} contractHash
     * @returns
     */
    transfer(amount, toHash, contractHash, asset) {
        
        return client.api.asset.send({
                to: toHash,
                asset,
                amount
            })
    }
}