let Ont = require("ontology-ts-sdk")

/**
 * address转hex
 * @param {*} address
 * @returns
 */
export function address2Hex(address) {
  try {
    return new Ont.Crypto.Address(address).serialize();
  } catch (err) {
    console.log(err);
    return "";
  }
}

/**
 * hex转address
 * @param {*} hex
 * @returns
 */
export function hex2Address(hex) {
  try {
    return new Ont.Crypto.Address(hex).toBase58();
  } catch (err) {
    console.log(err);
    return "";
  }
}

