import { sha256 } from "js-sha256";

const ROOT = "ont";

/**
 * 将字符串处理成ascii码十六进制拼接的字符串
 * @param {String} str
 * @returns
 */
export function bin(str) {
  if (!str) return "";
  return str
    .split("")
    .map(item => item.charCodeAt().toString(16))
    .join("");
}

/**
 * 字符串转字节流
 * @param {String} str
 * @returns
 */
export function str2Bytes(str) {
  let pos = 0;
  let len = str.length;
  if (len % 2 != 0) return null;
  len /= 2;
  let hexA = new Array();
  for (let i = 0; i < len; i++) {
    let s = str.substr(pos, 2);
    let v = parseInt(s, 16);
    hexA.push(v);
    pos += 2;
  }
  return hexA;
}

/**
 * 生成域名hash值
 * @param {String} url
 */
export function genDomainHash(domain) {
  if (domain) {
    return sha256(str2Bytes(sha256(ROOT) + bin(domain)));
  }
  return "";
}
