<template>
    <div class="home-swiper-panel" id="partner">
        <div class="home-swiper-title">{{$t('home.nav.partner')}}</div>
        <div class="home-swiper-content">
            <div class="partner-list" v-for="(item, index) in partners" :key="index">
                <div class="partner-item" v-for="(sub, subIndex) in item" :key="subIndex">
                    <img :src="sub.img" :alt="sub.name">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            partners: []
        }
    },
    mounted() {
        const list = this.$config.partners
        let arr = []
        let current = 1
        list.filter((item, index) => {
            if(index < 4 * current) {
                if(!arr[current - 1]) arr[current - 1] = []
                arr[current - 1].push(item)
            } else {
                arr[current] = []
                arr[current].push(item)
                current++
            }
        })
        this.partners = arr
    }
}
</script>

<style lang="less">
#partner {
    .home-swiper-title {
         margin-bottom: 80px;
    }
}
.partner-list {
    display: flex;
    justify-content: center;
}
.partner-item {
    box-sizing: border-box;
    width: 25%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
        max-height: 60px;
        max-width: 100%;
    }
}
</style>
