<template>
    <div class="home">
        <div class="home-header">
            <img class="home-logo" :src="$config.logo.white" alt="">
            <ul class="home-nav">
                <template v-for="item in navs">
                    <li  :key="item.label" v-if="item.id">
                        <a :href="'#' + item.id">{{$t('home.nav.' + item.id)}}</a>
                    </li>
                </template>
                <li><a :href="whitePaperLink" target="_blank">{{$t('home.nav.whitePaper')}}</a></li>
                <li v-if="loginStatus" @click="$refs.donation.open()">{{$t('home.nav.donation')}}</li>
                <li @click="changeLang">{{$t('lang')}}</li>
                <li v-if="loginStatus" @click="toMinePage">{{$t('home.nav.mine')}}</li>
            </ul>
        </div>
        <component :is="item.component" :style="{backgroundColor: item.color ? item.color : ''}" v-for="item in navs" :key="item.label"></component>
        <donation ref="donation"></donation>
        <my-footer></my-footer>
    </div>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'
import { INVITER_SCRIPT_HASH } from '@/store/types'
import Domain from './Domain'
import Scenes from './Scenes'
import Partner from './Partner'
import About from './About'
import WhitePaper from './WhitePaper'
import RoadMap from './RoadMap'
import MyFooter from './Footer'
import Donation from '@/components/modal/Donation'

export default {
    components: {
        Domain,
        About,
        Scenes,
        Partner,
        WhitePaper,
        MyFooter,
        RoadMap,
        Donation
    },
    data() {
        return {
            current: 0,
            navs: [
                {
                    component: 'domain',
                    color: '',
                    id: ''
                },
                {
                    component: 'scenes',
                    color: '#041f32',
                    id: 'scenes'
                },
                 {
                    component: 'road-map',
                    color: '#09283d',
                    id: 'roadmap'
                },
                {
                    component: 'partner',
                    color: '#041f32',
                    id: 'partner'
                },
                {
                    component: 'about',
                    color: '#09283d',
                    id: 'about'
                }
            ]
        }
    },
    mounted() {
        const ref =this.$route.query.ref
        // 获取url邀请者address
        if(ref && ref !== this.scriptHash) {
            this.setInviterHash(this.$util.address2Hex(ref))
        }
    },
    methods: {
        ...mapMutations({
            setInviterHash: INVITER_SCRIPT_HASH
        }),
        toMinePage() {
            this.$router.push({name: 'mine'})
        },
        changeLang() {
            let lang = this.$i18n.locale
            if(lang === 'en') lang = 'zh'
            else lang = 'en'
            this.$i18n.locale = lang
            window.localStorage.setItem("locale", lang)
        },
        login() {

        }
    },
    computed: {
        ...mapGetters(['loginStatus', 'scriptHash']),
        mySwiper() {
            return this.$refs.mySwiper.swiper
        },
        whitePaperLink() {
            return this.$config[this.$t('whitePiperSource')]
        }
    }
}
</script>

<style lang="less">
.home {
    position: relative;
    overflow-y: auto; 
    width: 100%;
    height: 100%;
    color: #fff;
}
.home-header {
    position: absolute;
    z-index: 9;
    box-sizing: border-box;
    width: 100%;
    height: 60px;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    opacity: 0.9;
    line-height: 1;
    background: linear-gradient(to bottom,  rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0))
}
.home-logo {
    height: 40px;
}
.home-nav {
    margin: 0;
    margin-right: -15px;
    padding: 0;
    overflow: hidden;
    list-style-type: none;
    font-size: 16px;
    li {
        display: inline-block;
        // height: 1em;
        padding: 15px;
        cursor: pointer;
        vertical-align: middle;
        a {
            text-decoration: none;
            color: #fff;
        }
    }
    li.active {
        position: relative;
        &::before {
            position: absolute;
            content: '';
            width: 2em;
            height: 2px;
            margin-left: 50%;
            bottom: 0;
            left: -1em;
            background: #fff;
            border-radius: 2px;
            animation-duration: 0.3s;
            animation-fill-mode: both;
            animation-name: fadeInLeft;
        }
    }
}
.swiper-container {
    width: 100%;
    height: 100%;
}
@keyframes fadeInLeft {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.home-swiper-panel {
    box-sizing: border-box;
    width: 100%;
    // height: 100%;
    padding: 10% 0;
    text-align: center;
}
.home-swiper-title {
    margin-bottom: 100px;
    font-size: 30px;
}
.home-swiper-content {
    width: 1000px;
    margin: auto;
    line-height: 1.5;
}
</style>
