import userService from '@/services/user'
import walletService from '@/services/wallet'
import { ACCOUNT, DOMAINS, LOGIN_STATUS, EMPTY_USER, ADDRESS, SCRIPT_HASH, DONAT_TOTAL, INVITER_TOTAL, BALANCE } from './types'

export default {
    // 获取账户
    getAccount({
        dispatch,
        commit
    }) {
        userService.getAccount().then(res => {
            commit(LOGIN_STATUS, true)
            commit(ACCOUNT, res)
            commit(ADDRESS, res)
            commit(SCRIPT_HASH, res)
            dispatch('getDonatTotal')
            dispatch('getInviterTotal')
            dispatch('getDomains')
            dispatch('getBalance')
        }).catch(err => {
            console.log('初始化用户信息出错')
            console.log(err)
            commit(EMPTY_USER)
        })
    },

    // 获取钱包余额
    getBalance({
        commit
    }) {
        console.log('获取钱包余额')
        walletService.getBalance().then(res => {
            console.log('获取钱包余额成功')
            console.log(res)
            commit(BALANCE, { ONG: Number(res.ONG), ONT: Number(res.ONT)})
        }).catch(err => {
            console.log('获取钱包失败')
            console.log(err)
        })
    },

    // 获取用户捐赠总额
    getDonatTotal({
        commit,
        state
    }) {
        walletService.getDonatTotal(state.scriptHash).then(res => {
            commit(DONAT_TOTAL, parseInt(res, 16))
            console.log(res)
            console.log('获取用户捐赠总额')
        }).catch(err => console.log(err))
    },

    // 获取邀请用户数
    getInviterTotal({
        commit
    }) {
        userService.getInviteTotal().then(res => {
            commit(INVITER_TOTAL, parseInt(res, 16))
            console.log(res)
            console.log('获取邀请用户数')
        }).catch(err => console.log(err))
    },

    // 获取域名列表
    getDomains({
        commit
    }) {
        userService.getDomains().then(res => {
            commit(DOMAINS, res.data.domains.map(item => ({
                name: item.names.split('.')[0],
                time: item.time
            })))
        }).catch(err => console.log(err))
    }
}