<template>
    <div class="home-domain">
        <div class="home-domain-mask"></div>
        <div>
            <div class="search-remark">
                <h3>{{$t('home.domain.title')}}</h3>
                <p>{{$t('home.domain.subtitle')}}</p>
                <p @click="info" class="home-rule-btn">{{$t('rule.btn')}}</p>
            </div>
            <div class="search-input-wrap">
                <input v-model="form.value" type="text" :maxlength="30" :placeholder="$t('home.domain.placeholder')" @pressEnter="submit" @change="changeValue">
                <span>.ont</span>
                <a-button size="large" :loading="loading" type="primary" @click="submit">{{loading ? $t('home.domain.regBtning') : $t('home.domain.regBtn')}}</a-button>
                <!-- <span @click="submit">{{loading ? $t('home.domain.regBtning') : $t('home.domain.regBtn')}}</span> -->
            </div>
            <!-- <div class="search-input-err-txt" v-if="errorTxt">{{$t('home.domain.tip')}}{{errorTxt}}</div> -->
        </div>
        <a class="home-more" href="#scenes">
            <p>more</p>
            <img :src="require('@/assets/images/icons/arrows_down.png')" alt="">
        </a>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import domainService from '@/services/domain'

export default {
    data() {
        return {
            loading: false,
            form: {
                value: ''
            },
            errorTxt: ''
        }
    },
    methods: {
        submit() {
            // console.log(this.$util.genDomainHash(this.form.value))
            if(!this.loading) {
                const domain = this.form.value
                if(!this.loginStatus) return this.showLoginErrInfo()
                if(!domain) return this.$message.warning(this.$t('dictionary.domainNull'))
                if(!this.$util.verifyDomain(domain)) return this.$message.warning(this.$t('dictionary.domainIllegalFormat'))
                this.loading = true
                domainService.rgister(this.form.value)
                .then(res => {
                   const code = parseInt(res.result[0][1], 16)
                    if(code === 14) {
                        this.$message.success(this.$t('dictionary.registerSuccess'))
                        // this.getDomains()
                        this.$router.push({name: 'mine', query: { type: 'domain'}})
                    } 
                    else this.$message.warning(this.$t('register.' + this.$config.registerStatus[code]))
                })
                .catch(err => {
                    console.log(err)
                    this.$message.warning(this.$t('dictionary.registerError'))
                })
                .finally(() => this.loading = false)
            }
        },
        info() {
            const h = this.$createElement
            this.$info({
                title: this.$t('rule.title'),
                content: h('div',{}, [
                    h('p', this.$t('rule.0')),
                    h('p', this.$t('rule.1')),
                    h('p', this.$t('rule.2'))
                ]),
                onOk() {},
            })
        },
        changeValue() {
            this.errorTxt = ''
        },
        showLoginErrInfo() {
            const h = this.$createElement
            this.$info({
                title: this.$t('dictionary.tip'),
                content: h('div',{}, [
                    h('p', this.$t('dictionary.notLoginInfo')),
                    h('a', { attrs: { href: 'https://chrome.google.com/webstore/detail/cyano-wallet/dkdedlpgdmmkkfjabffeganieamfklkm?utm_source=chrome-ntp-icon' }}, 'cyano-wallet'),
                ]),
                onOk() {},
            });
        }
    },
    computed: {
        ...mapGetters(['loginStatus'])
    }
}
</script>

<style lang="less">
.home-domain {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    // align-items: center;
    padding-top: 15%;
    text-align: center;
    background: url('http://qiniu.mysprout.cn/bg.946f57e5.jpeg');
    background-size: cover;
    background-position: center; 
}
.home-domain-mask {
    position: absolute;
    width: 100%;
    height: 30%;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to top , #041f32, rgba(0, 0, 0, 0));
}
.home-more {
    position: absolute;
    width: 36px;
    height: 36px;
    margin-left: 50%;
    bottom: 20px;
    left: -15px;
    text-decoration: none;
    font-size: 14px;
    h3, p {
        color: #fff;
    }
    p {
        margin: 0;
        padding: 0;
    }
    img {
        width: 20px;
        height: 20px;
        vertical-align: bottom;
    }
}
.search-remark {
    margin-bottom: 24px;
    // opacity: 0.9;
    * { 
        color: #fff;
    }
    h3 {
        font-weight: 500;
        font-size: 30px;
        margin-bottom: 10px;
    }
    p {
        font-size: 16px;
    }
}
.search-input-wrap {
    width: 500px;
    display: flex;
    align-items: center;
    margin: auto;
    padding: 5px 7px;
    border: 2px solid #fff;
    background-color: #fff;
    border-radius: 4px;
    color: #333;
    input {
        width: 100%;
        flex: 1;
        padding: 8px 15px;
        background-color: transparent;
        border: none;
        resize: none;
        outline: 0;
        font-size: 16px;
        line-height: 25px;
    }
    &>span {
        margin-right: 15px;
        font-size: 16px;
        // padding: 15px;
        // text-align: center;
        // cursor: pointer;
    }
}
.search-input-err-txt {
    padding: 10px;
    font-size: 14px;
}
.search-input-root {
    font-size: 16px;
}
.home-rule-btn {
    cursor: pointer;
}
</style>
