<template>
     <a-modal
            :title="$t('home.nav.donation')"
            :visible="show"
            @ok="ok"
            :confirmLoading="loading"
            @cancel="cancel"
            >
      <div class="donation-wrap">
          <div class="donation-title">{{$t('dictionary.balance')}}：<span class="donation-balance">{{balance.ONT}}</span> ONT，{{$t('dictionary.donation')}}：</div>
          <div><a-input-number class="donation-input" size="large" :min="1" :max="999" :step="1" :precision="0" v-model="form.value" @change="onChange" /> ONT</div>
      </div>
    </a-modal>
</template>

<script>
import walletServices from '@/services/wallet' 
import { mapActions, mapGetters } from 'vuex'

export default {
    data() {
        return {
            show: false,
            loading: false,
            form: {
                value: 1
            }
        }
    },
    methods: {
        ...mapActions(['getDonatTotal', 'getBalance']),
        open() {
            this.show = true
        },
        close() {
            this.show = false
            this.form.value = 1
        },
        ok() {
            this.loading = true
            walletServices.donat(this.form.value).then(res => {
                const code = parseInt(res.result[0][1], 16)
                if(code === 14) {
                    this.$message.success(this.$t('dictionary.donationSuccess'))
                    this.close()
                    this.getDonatTotal()
                    this.getBalance()
                } 
                else this.$message.warning(this.$t('register.' + this.$config.registerStatus[code]))
            }).catch(err => {
                console.log(err)
            }).finally(() => this.loading = false)
        },
        cancel() {
            this.close()
        },
        onChange(value) {
            // this.form.value = parseInt(value)
        }
    },
    computed: {
        ...mapGetters(['balance'])
    }
}
</script>

<style lang="less">
.donation-wrap {
    font-size: 20px;
}
.donation-title {
    margin-bottom: 10px;
    font-size: 16px;
}
.donation-input {
    width: 8em;
    margin-right: 5px;
}
.donation-balance {
    font-weight: bold;
}
</style>
