import store from '@/store'
import { sha256 } from 'js-sha256'
import { genDomainHash } from '@/tools/hex'
import { ontDApi } from '@/tools/ontDapi'

const root = CONFIG.root                                                    // 域名后缀
// const resultCode = config.domainActionResultCode                         // 返回状态码

export default {
    /**
     * 注册
     * @param {String} name 
     */
    rgister(name = '') {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },           // 调用者自身的scripthash
            { type: 'String', value: name },                                // 域名名称，例如test123.ont 注册的name为test123
            { type: 'ByteArray', value: sha256(root)},                      // 根域名的hash，例如test123.ont的根为ont,这里的值为sha256("ont")
            { type: 'ByteArray', value: store.state.inviterScriptHash}      // 邀请者的scripthash
        ]
        return ontDApi({
            args,
            type: 'post',
            method: 'Register'
        })
    },

    /**
     * 设置解析
     * @param {String} name
     * @param {Number} code
     * @param {String} content
     * @returns
     */
    setResolve(name, code, content) {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },           // 调用者自身的scripthash
            { type: 'ByteArray', value: genDomainHash(name) },              // 域名hash
            { type: 'Number', value: code},                                 // 协议定义码
            { type: 'String', value: content}                               // 设置的解析内容
        ]
        console.log(args)
        return ontDApi({
            args,
            type: 'post',
            method: 'SetResolve'
        })
    },

    /**
     * 解析
     * @param {String} name 域名
     * @param {Number} code 协议定义码
     * @returns
     */
    resolve(name, code) {
        const args = [
            { type: 'ByteArray', value: genDomainHash(name) },              // 域名hash
            { type: 'Number', value: code},                                 // 协议定义码
        ]
        return ontDApi({
            args,
            method: 'Resolve'
        })
    },

    /**
     * 转移
     * @param {String} to 接收者address
     * @param {String} name 域名
     * @returns
     */
    transfer(to, name) {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },           // 调用者自身的scripthash
            { type: 'ByteArray', value: to },                               // 接受者的scripthash
            { type: 'ByteArray', value: genDomainHash(name)},               // 域名的hash
        ]
        return ontDApi({
            args,
            type: 'post',
            method: 'Transfer'
        })
    },

    /**
     * 销毁
     * @param {String} name 域名
     * @returns
     */
    drop(name) {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },           // 调用者自身的scripthash
            { type: 'ByteArray', value: genDomainHash(name) },              // 域名hash
        ]
        return ontDApi({
            args,
            type: 'post',
            method: 'Drop'
        })
    },

    /**
     * 查询域名拥有者
     * @param {String} name 域名
     * @returns
     */
    queryByuser(name) {
        const args = [
            { type: 'ByteArray', value: genDomainHash(name) },              // 域名hash
        ]
        return ontDApi({
            args,
            method: 'GetDomainReg'
        })
    },

}