import store from '@/store'
import { client } from 'ontology-dapi'
let Ont = require('ontology-ts-sdk')
import { ontDApi } from '@/tools/ontDapi'
import userApi from '@/api/user'

export default {
    /**
     * 获取账户信息
     */
    async getAccount() {

        client.registerClient({})
        const account = await client.api.asset.getAccount()
        const address = new Ont.Crypto.Address(account)
        const scriptHash = address.serialize()
        return {
            account,
            address: address.value || '',
            scriptHash
        };
    },

    /**
     * 获取域名列表
     */
    getDomains() {
        return userApi.getDomains(store.state.address)
    },

    /**
     * 获取邀请数
     * 阅读注册域名规则，邀请数与注册域名有关
     */
    getInviteTotal() {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },                     // 调用者自身的scripthash
        ]
        return ontDApi({
            args,
            method: 'GetInvitCountForUser'
        })
    }
    
}